export const getTimeValue = (time, type) => {
  const obj = {
    // 
    year: time.getFullYear(),
    // 
    month: time.getMonth() + 1,
    // 
    dateDay: time.getDate(),
    // 
    weekDay: time.getDay() + 1,
    // 
    hours: time.getHours(),
    // 
    minutes: time.getMinutes()
  }
  if (type !== undefined) {
    return obj[type]
  }
  
  return obj
}


