<template>
  <div class="customer-service-rating">
    {{ 
      `${label}${supportStaffAverageScore !== null ? supportStaffAverageScore +' ': ''}${supportStaffScoreTimes !== null ? supportStaffScoreTimes +'': ''}`
    }}
  </div>
</template>

<script>


export default {
  name: 'customer-service-rating',
  props: {
    //
    supportStaffAverageScore: {
      type: Number,
      default: null
    },
    //
    supportStaffScoreTimes: {
      type: Number,
      default: null
    },
    //''
    label: {
      type: String,
      default: ''
    }
  },
}
</script>



